.main__descript { font-size: 14px; font-weight: 400; color: #999999; }

.main__row { display: flex; flex-wrap: wrap; justify-content: center; align-items: center; }

.card { width: 100%; max-width: 520px; padding: 40px 50px; border-radius: 25px; box-shadow: 0 10px 20px rgba(0, 0, 0, 0.07); background-color: #ffffff; margin-bottom: 20px; }

.card:not(:last-child) { margin-right: 20px; }

@media only screen and (max-width: 1023px) { .card:not(:last-child):not(:last-child) { margin-right: 0; } }

@media only screen and (max-width: 767px) { .card { padding: 20px 25px; } }

.card__title { text-align: center; padding-bottom: 22px; margin-bottom: 25px; border-bottom: 1px solid #dadada; }

.card__text { font-size: 14px; font-weight: 300; line-height: 1.2; color: #777777; margin-bottom: 33px; }

@media only screen and (max-width: 767px) { .card__text { margin-bottom: 20px; } }

.card__btn { text-align: center; }

.slider { position: relative; overflow: hidden; margin-bottom: 40px; }

@media only screen and (max-width: 767px) { .slider { margin-bottom: 20px; } }

.slider__wrap { position: relative; opacity: 0; visibility: hidden; transition: opacity .5s, visibility .5s; }

.slider__wrap.slick-initialized { overflow: visible; opacity: 1; visibility: visible; }

.slider__slide-for { position: relative; display: block; max-width: 340px; max-height: 255px; min-width: 255px; text-align: center; }

.slider__slide-for img { display: inline-block; max-width: 100%; max-height: 255px; min-height: 255px; }

.slider__slide-for::before { position: absolute; left: 60%; top: 50%; transform: translate(-50%, -50%); width: 40px; height: 40px; content: ''; border-radius: 10px; background-color: rgba(0, 0, 0, 0.5); opacity: 0; transition: opacity .3s; cursor: pointer; }

.slider__slide-for::after { position: absolute; left: 60%; top: 50%; transform: translate(-50%, -50%); width: 20px; height: 20px; content: ''; background-image: url(../img/search.png); background-repeat: no-repeat; background-position: 50% 50%; background-size: cover; opacity: 0; transition: opacity .3s; cursor: pointer; }

.slider__slide-for:hover::before, .slider__slide-for:hover::after { opacity: 1; }

@media only screen and (max-width: 767px) { .slider__slide-for { max-width: 100%; height: 350px; }
  .slider__slide-for::before, .slider__slide-for::after { left: 50%; } }

@media only screen and (max-width: 599px) { .slider__slide-for { min-height: auto; } }

.slider__nav { position: absolute; top: 0; right: 0; width: 70px; }

@media only screen and (max-width: 767px) { .slider__nav { width: 100%; position: static; } }

.slider__slide-nav { position: relative; width: 69px; height: 70px; border-radius: 10px; border: 1px solid #dadada; background-color: #ffffff; cursor: pointer; transition: border-color .3s; overflow: hidden; margin-bottom: 15px; font-size: 0; text-align: center; }

.slider__slide-nav img { display: inline-block; max-width: 100%; }

@media only screen and (max-width: 767px) { .slider__slide-nav { line-height: 95px; margin-bottom: 0; width: 100%; height: 95px; }
  .slider__slide-nav img { max-width: 70px; height: 70px; vertical-align: middle; } }

@media only screen and (max-width: 599px) { .slider__slide-nav { height: 70px; line-height: 70px; } }

.slider__slide-nav::after { position: absolute; top: 0; right: 0; bottom: 0; left: 0; border-radius: 10px; content: ''; background-color: rgba(255, 255, 255, 0.5); opacity: 1; transition: opacity .3s; }

.slider .slick-slide.slick-current.slick-active .slider__slide-nav { border-color: #000000; }

.slider .slick-slide.slick-current.slick-active .slider__slide-nav::after { opacity: 0; }

.slider__slides { padding-right: 15px; }

.overlay { z-index: 99; position: fixed; background-color: #000000; opacity: 0.8; filter: alpha(opacity=80); top: 0; right: 0; bottom: 0; left: 0; cursor: pointer; display: none; }

.popup { display: none; position: fixed; min-width: 280px; min-height: 0px; max-width: 826px; width: 100%; border-radius: 25px; background-color: #ffffff; top: 50%; left: 50%; z-index: 100; transform: translate(-50%, -50%); }

@media only screen and (max-width: 767px) { .popup { top: 0; right: 0; bottom: 0; left: 0; transform: translate(0, 0); border-radius: 0; } }

.popup__wrap { position: relative; border-radius: 25px; width: 100%; height: 100%; overflow: hidden; padding: 40px 50px; -webkit-overflow-scrolling: touch; }

.popup__wrap::after { position: absolute; top: -50px; left: -40px; width: 250px; height: 250px; content: ''; background-image: url(../img/smart1.png); background-repeat: no-repeat; background-position: 50% 50%; z-index: -1; }

@media only screen and (max-width: 767px) { .popup__wrap { padding: 45px 25px 30px; overflow-y: scroll; }
  .popup__wrap::after { display: none; } }

.popup__close { position: absolute; top: 30px; right: 30px; transform: translate(50%, -50%); }

.popup__title { margin-bottom: 5px; text-align: center; }

.popup__descript { padding-bottom: 10px; margin-bottom: 30px; font-size: 13px; font-weight: 300; color: #999999; text-align: center; border-bottom: 1px solid #dadada; }

.popup__row { display: flex; align-items: flex-start; justify-content: space-between; margin-bottom: 25px; }

@media only screen and (max-width: 767px) { .popup__row { flex-wrap: wrap; } }

.popup__col { flex: 1 1 50%; }

.popup__col:not(:last-child) { margin-right: 30px; }

@media only screen and (max-width: 767px) { .popup__col { flex: 1 1 100%; }
  .popup__col:not(:last-child) { margin-right: 0; margin-bottom: 30px; } }

.popup__col-title { text-align: center; margin-bottom: 30px; }

.popup__col-img { width: 100%; margin: 0 auto; }

.popup__col-img img { width: 100%; }

.popup__btn { text-align: center; }

.popup__btn .btn { min-width: 186px; }

.form__row:not(:last-child) { margin-bottom: 15px; }

.form__row--mod { font-size: 12px; font-weight: 300; color: #999999; }

.form__input { width: 100%; height: 46px; padding: 13px 24px 12px; border-radius: 23px; border: 1px solid rgba(218, 218, 218, 0.7); box-sizing: border-box; font-family: 'Roboto'; font-size: 15px; font-weight: 300; color: #494949; transition: background-color .3s; box-shadow: inset -2px 2px 7px rgba(0, 0, 0, 0.05); }

.form__input::placeholder { font-family: "Roboto"; font-family-font-size: 15px; font-family-font-weight: 300; font-family-color: #494949; }

.form__input:focus { background-color: #f4f5f8; }

.form__input:valid { background-color: #f4f5f8; }

.close { display: inline-block; position: relative; width: 22px; height: 22px; border: none; background: none; }

.close::before { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%) rotate(45deg); width: 100%; height: 2px; content: ''; border-radius: 2px; background-color: #494949; transition: background-color .3s; }

.close::after { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%) rotate(-45deg); width: 100%; height: 2px; content: ''; border-radius: 2px; background-color: #494949; transition: background-color .3s; }

.close:hover::before, .close:hover::after { background-color: #c4132f; }

.btn { display: inline-block; background: none; font-family: Roboto; font-size: 16px; font-weight: 300; line-height: 1; letter-spacing: .8px; color: #008348; text-decoration: none; text-align: center; }

.btn__box { display: block; position: relative; min-width: 155px; height: 46px; box-sizing: border-box; text-align: center; }

.btn__box:hover .btn__icon { transform: translateY(100%) rotateX(90deg); }

.btn__box:hover .btn__details { transform: translateY(0) rotateX(0deg); }

@media only screen and (max-width: 1023px) { .btn__box:hover .btn__icon { transform: translateY(0) rotateX(0deg); }
  .btn__box:hover .btn__details { transform: translateY(-46px) rotateX(90deg); } }

.btn.success .btn__icon { transform: translateY(100%) rotateX(90deg); }

.btn.success .btn__details { transform: translateY(0) rotateX(0deg); background-color: #008348; }

.btn.success .btn__details::before { position: absolute; top: 50%; left: 45px; transform: translate(-50%, -50%); width: 14px; height: 12px; content: ''; background-image: url(../img/check.png); background-repeat: no-repeat; background-position: 50% 50%; background-size: cover; }

.btn__icon { position: relative; display: block; width: 100%; height: 100%; padding: 13px 34px 12px; box-shadow: 0 5px 20px rgba(0, 131, 72, 0.15); line-height: 20px; border-radius: 23px; border: 1px solid rgba(0, 131, 72, 0.65); background-color: #ffffff; transition: transform .5s; transform-origin: top; transform: translateY(0) rotateX(0deg); }

.btn__details { display: block; position: absolute; top: 0; left: 0; width: 100%; height: 100%; padding: 13px 34px 12px; box-shadow: 0 5px 20px rgba(0, 131, 72, 0.15); line-height: 20px; color: #ffffff; border-radius: 23px; border: 1px solid rgba(0, 131, 72, 0.65); background-color: #ffffff; transition: transform .5s; background-color: #1bab0e; transform-origin: bottom; transform: translateY(-46px) rotateX(90deg); }

.btn.btn--mod .btn__icon { box-shadow: none; background-color: transparent; border: 2px solid #ffffff; color: #ffffff; }

.hero.inner { padding: 0; }

.hero__img-up, .hero__img-bottom { position: absolute; background-repeat: no-repeat; background-position: 50% 50%; background-size: cover; z-index: 1; }

@media only screen and (max-width: 767px) { .hero__img-up, .hero__img-bottom { display: none; } }

.hero__img-up { top: 80px; left: calc(50% - 540px); width: 232px; height: 245px; }

.hero__img-bottom { left: calc(50% + 240px); bottom: 0; width: 460px; height: 500px; }

.hero__wrap { position: fixed; min-width: 280px; min-height: 0px; max-width: 790px; padding: 70px 114px 91px; width: 100%; border-radius: 25px; background: #ffffff; top: 40%; left: 50%; z-index: 100; transform: translate(-50%, -50%); font-family: 'Roboto'; font-size: 14px; font-weight: 400; line-height: 18px; color: #999999; }

@media only screen and (max-width: 767px) { .hero__wrap { position: static; padding: 35px 25px 45px; transform: translate(0); border-radius: 0; } }

.hero__title { text-align: center; margin-bottom: 40px; }

@media only screen and (max-width: 767px) { .hero__title { margin-bottom: 20px; } }

.hero__descrip { margin-bottom: 70px; }

@media only screen and (max-width: 767px) { .hero__descrip { text-align: center; margin-bottom: 35px; } }

.hero__row { display: flex; align-items: flex-start; justify-content: space-between; }

@media only screen and (max-width: 767px) { .hero__row { flex-wrap: wrap; align-items: center; justify-content: center; } }

.hero__col--left { flex: 1 1 70%; }

@media only screen and (max-width: 767px) { .hero__col--left { flex: 1 1 100%; margin-bottom: 25px; } }

.hero__btn { text-align: center; }

@media only screen and (max-width: 767px) { .hero__btn { flex: 1 1 100%; } }

.hero__col:not(:last-child) { margin-right: 30px; }

@media only screen and (max-width: 767px) { .hero__col:not(:last-child) { margin-right: 0; } }

.form--mod { width: 100%; }

.form--mod .form__row { position: relative; overflow: hidden; max-width: 390px; }

@media only screen and (max-width: 767px) { .form--mod .form__row { max-width: 100%; } }

.form--mod .form__row .after-affect { position: absolute; display: block; width: 100%; height: 1px; left: 0; bottom: 0; content: ''; background-color: #1bab0e; transform: translateX(-100%); transition: transform .3s; }

.form--mod .form__input { border-radius: 0; border: 0; border-bottom: 1px solid #dadada; padding-left: 10px; }

.form--mod .form__input:focus, .form--mod .form__input:valid { border-color: transparent; background-color: transparent; }

.form--mod .form__input:focus ~ .after-affect, .form--mod .form__input:valid ~ .after-affect { transform: translateX(0); }

.header--mod { position: absolute; top: 0; left: 0; right: 0; z-index: 3; }

@media only screen and (max-width: 767px) { .header--mod { position: static; } }

.feedback { z-index: 99; position: fixed; background-color: #000000; opacity: 0.8; filter: alpha(opacity=80); top: 0; right: 0; bottom: 0; left: 0; cursor: pointer; }

.window { position: fixed; min-width: 280px; min-height: 0px; max-width: 558px; padding: 50px 40px; width: 100%; border-radius: 25px; background: #ffffff; top: 50%; left: 50%; z-index: 100; transform: translate(-50%, -50%); text-align: center; font-family: 'Roboto'; font-size: 14px; font-weight: 400; line-height: 18px; color: #999999; }

.window__title { margin-bottom: 20px; }

.window p { margin-bottom: 45px; }

@media only screen and (max-width: 767px) { .window__title { margin-bottom: 10px; }
  .window p { margin-bottom: 25px; } }

.footer { padding: 35px 0 31px; background-color: #363636; opacity: 0.8; }

@media only screen and (max-width: 767px) { .footer { padding: 17px 0 15px; } }

.footer.footer--mod { position: absolute; left: 0; right: 0; bottom: 0; padding: 22px 0; z-index: 3; }

@media only screen and (max-width: 767px) { .footer.footer--mod { position: static; } }

.footer.footer--mod .footer__text { text-align: left; line-height: 1.3; }

.footer__text { font-size: 14px; font-weight: 300; line-height: 1.8; color: #ffffff; text-align: center; }

.footer__row { display: flex; align-items: center; justify-content: space-between; }

@media only screen and (max-width: 767px) { .footer__row { flex-wrap: wrap; } }

.footer__col:not(:last-child) { margin-right: 30px; }

@media only screen and (max-width: 767px) { .footer__col { flex: 1 1 100%; text-align: center; }
  .footer__col:not(:last-child) { margin-right: 0; margin-bottom: 30px; } }

.footer__col--right { font-size: 0; }
